.post {
    max-width: 850px;
    height: 100%;
    margin: 0 auto;
    padding: 5px 30px 30px 30px;
    background-color: rgb(248, 248, 248);
    color: black;
    border-radius: 6px;
}

.markdown {
    p {
        margin: 2px 0 2px 0;
    }
}

@media screen and (max-width: 1000px) {
    .post {
        width: 93%;
    }
}